import React, { Component } from 'react';
import './HeroBottom-component.scss';


class HeroBottom extends Component {
    state = {
        windowHeight: Number,
        bottomHeight: Number,
    };
    componentDidMount() {
        this.handleResize();
        window.addEventListener('resize', this.handleResize)
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize)
    }
    handleResize = () => {
        this.setState({ windowHeight: window.innerHeight - 56 })
        this.setState({ bottomHeight: document.getElementById('Bottom').clientHeight})
    }

    render() {
        return (
                <div id="Bottom" className="Hero-Bottom bg-dark  text-white" style={{width: "100%", maxHeight: 500}}>
                    <div className="container">
                        <div className="row  bottom">
                            <div className="col-xl text-danger" style={{marginBottom: '1rem'}} >
                                <h1>
                                    Tommy Ballo;
                                </h1>
                                
                            </div>
                            <div className="col-md" style={{}}>
                                <h5>Hva er Pc-Digg?</h5>
                                <p>Denne websiden er et prosjekt Tommy Ballo har satt sammen for eksprimentering med ulike web teknologier.</p>
                            </div>
                            <div className="col-md">
                                <h5>Hva er planen?</h5>
                                <p>
                                    Etter planen skal denne websiden brukes til testing av React.js, Angular, Rest-APIer, websockets og andre teknologier som treffes på veien.
                            </p>
                            </div>
                        </div>
                    </div>
                </div>
        );
    }
}

export default HeroBottom;
