import React, { Component } from 'react';
import './Hero.component.scss';
import HeroBottom from './HeroBottom-component/HeroBottom-component';
import LargeButton from '../../reusable-components/Buttons/LargeButton.component';
import Progresjon from '../Progresjon/Progresjon.component';


class Front extends Component {
    state = {
        windowHeight: 0,
        headerHeight: 0,
        bottomHeight: 0,
        heroHeight: 0,
    };
    componentDidMount() {
        this.handleResize();
        window.addEventListener('resize', this.handleResize);
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }
    handleResize = () => {
        this.setState({ 
            windowHeight: window.innerHeight,
            headerHeight: document.getElementById('Header').clientHeight,
            bottomHeight: document.getElementById('Bottom').clientHeight,
            heroHeight: window.innerHeight - document.getElementById('Header').clientHeight - document.getElementById('Bottom').clientHeight
        });
    }


    render() {
        return (
            <div className="bg-primary">
                <div className="container">
                    <div className="row" style={{ height: this.state.heroHeight, minHeight:300}}>
                        <dir className="col-lg text-left text-white" style={{ marginTop: "auto", marginBottom: "auto" }}>
                            <h1>Et offentlig test miljø for alt som er Pc-Digg</h1>
                            <LargeButton></LargeButton>
                           
                        </dir>
                    </div>
                </div>

                <HeroBottom></HeroBottom>
                <Progresjon></Progresjon>
            </div>
        );
    }
}

export default Front;
