import React, { Component } from 'react';
import './Progresjon.component.scss';
import Skillbar from '../../reusable-components/Skillbar/Skillbar.component';


class Progresjon extends Component {
    state = {
        windowHeight: Number,
        titleHeight: Number,
    };
    componentDidMount() {
        this.handleResize();
        window.addEventListener('resize', this.handleResize)
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize)
    }
    handleResize = () => {
        this.setState({ 
            windowHeight: window.innerHeight,
            titleHeight: document.getElementById('skillTitle').clientHeight
        });
    }
    render() {
        return (
            <div className="bg-primary" style={{ minHeight: this.state.windowHeight }}>
                <div id="skillTitle" className="container-flex text-dark bg-danger" style={{ paddingTop: "5rem", paddingBottom: "5rem", marginBottom: "1rem" }}>
                    <h1>Ferdigheter</h1>
                    <h4>Hva har Tommy plukket opp til nå?</h4>
                </div>
                <div style={{height: this.state.windowHeight - this.state.titleHeight, overflowY: "scroll"}}>
                <Skillbar skillName="Golang" info="Go er raskt og compilere, har ner C ytelse og en parralelliserings model som gjoor det morsomt og lage system applikasjoner. Dette har fort gjort Golang til mitt favoritt spraak."></Skillbar>
                <Skillbar skillName="JavaScript" info="ECMAScript er det eneste spraaket du ikke har noen valg om og bruke i dag. Hvis du har lyst at din applikasjon skal fungere paa web saa er JS er krav."></Skillbar>
                <Skillbar skillName="HTML/CSS" info="jeg er flink nokk i html og css til og designe denne nettsiden. Med litt hjelp av Bootstrap og React ble jo dette en lek."></Skillbar>
                <Skillbar skillName="C Sharp" info="C# er et av mine mest produktive spraak. Microsoft har gjort en fantastisk jobb med dokumentasjon, og de bygger kraftige rammeverk rundt .Net standard plattformen"></Skillbar>
                <Skillbar skillName="Java" info="Da jeg gikk paa ingenoor linja i kongsberg brukte vi java under oppleringen. Det er flere ting jeg liker med java som for eksempel hvor lett det er og kjoore paa forskjellige maskiner, men har ikke brukt java siden ingenioor klassen."></Skillbar>                
                </div>
            </div>
        );
    }
}
export default Progresjon;
